":require("../assets/roboticsDemo.mp4"),"type":"video/mp4"}})]),_vm._v(" Even though it wasn't used for training in the end because it wasn't realistic enough, I'm still very proud of what I created. I designed a modular networking system so that a game could be joined by any number of computers, each with any number of robots on them. You could even have a computer with zero robots which would just be a spectator. As you can see in the video, we split it up into two computers each with three computers. I set up a booth at the competition where people could play my game, and below is a photo! "),_c('img',{staticClass:"photo",attrs:{"src":require("../assets/roboticsPhoto.jpg")}}),_c('span',{staticClass:"ps"},[_vm._v("Disclaimer: the cover photo is the simulation I made the year after, which was in 3D but was singleplayer")])])])}]

